import React, { useEffect, useState } from "react"
import { PostingListType, usePostingList } from "../../../hooks/usePostingList"
import {useRecoilValue} from "recoil";
import { parameterNameState } from "../../../recoil/PostingListParamName";
import { blogContentId } from "../../../recoil/BlogContent";
import BookMarkBottom from "../element/BookMarkBottom";
import BookMarkList from "../element/BookMarkList";
import BookMarkSvg from "../element/BookMarkSvg";
import BookMarkTitle from "../element/BookMarkTitle";
import { SytledBlog } from "../../styledComponents/blog/StyledBlog";

const {CustomBookMark} = SytledBlog.molecules;

const BookMark = () => {

    const languageName: string = useRecoilValue(parameterNameState);
    const datas:PostingListType[] = usePostingList(languageName);
    const blogId:string = useRecoilValue(blogContentId);
    
    const [hide, setHide] = useState<boolean>(true);
    const [currentPage, setCurrentPage] = useState<number>(0);

    const setCurrentPageIdx = () => {

        datas.filter((data:PostingListType, index:number) => {
            if(data.id === blogId){
                setCurrentPage(index);
            }
        });

    }

    useEffect(()=>{
        setCurrentPageIdx()
    },[blogId]);
    
    return(
        <CustomBookMark hide={hide}>
            <BookMarkTitle />
            { hide ? <></> : <BookMarkList datas={datas} currentPage={currentPage} languageName={languageName}/>}
            <BookMarkSvg />
            <BookMarkBottom 
                hide={hide}
                setHide={setHide}
                currentPage={currentPage}
                languageName={languageName}
                datas={datas}
            />
        </CustomBookMark>
    )
}

export default BookMark;