import { Link } from "gatsby";
import React from "react";
import { useRecoilValue } from "recoil";
import { parameterNameState } from "../../../recoil/PostingListParamName";
import { SytledBlog } from "../../styledComponents/blog/StyledBlog";

const {CustomBookMarkTitle} = SytledBlog.elements;

const BookMarkTitle = () => {

    const languageName: string = useRecoilValue(parameterNameState);

    return(
        <CustomBookMarkTitle>
            <div className={'title_name'}>
                <Link to={`/postinglist/${languageName}`}>
                    {languageName.toLocaleUpperCase()}
                </Link> 
            </div>
        </CustomBookMarkTitle>
    )
}

export default BookMarkTitle;