import rehypeReact from "rehype-react";
import { unified } from "unified";
import React from "react";
import styled from "styled-components";
import { StyledAst } from "../styledComponents/blog/StyledAst";

const {CustomSpan, CustomP, CustomUl, CustomLi, CustomTable, CustomBlockquote} = StyledAst;

const CustomImage = styled.img.attrs({
  srcSet: ''})`
`
const processor = unified().use(rehypeReact, {
  createElement: React.createElement,
  components: {
    ul: CustomUl,
    li: CustomLi,
    table: CustomTable,
    blockquote: CustomBlockquote,
    p: CustomP,
    span: CustomSpan,
    img:CustomImage
  }
});

export const renderAst = (ast: any): JSX.Element => {

  return (processor.stringify(ast) as unknown) as JSX.Element
}