import { atom, selector } from "recoil";
import { PostingListType } from "../hooks/usePostingList";

export const blogContentState = atom({
    key: 'blogContent', 
    default: {} as PostingListType, 
});

export const blogContentTitle = selector({
    key: 'blogContentTitle',
    get: ({get}) => {
        const content = get(blogContentState);

        return content.frontmatter?.title;
    }
});

export const blogContentId = selector({
    key: 'blogContentId',
    get: ({get}) => {
        const content = get(blogContentState);

        return content.id;
    }
});
