import { Link } from "gatsby";
import React, { Dispatch, SetStateAction } from "react";
import { PostingListType } from "../../../hooks/usePostingList";
import SVG, { SvgImages } from "../../layouts/Svg";
import { SytledBlog } from "../../styledComponents/blog/StyledBlog";


const {CustomBookMarkBottom, CustomArrowBox} = SytledBlog.elements;

type PropsType = {
    datas: PostingListType[];
    currentPage: number;
    hide: boolean;
    setHide: Dispatch<SetStateAction<boolean>>;
    languageName:string;
}

const BookMarkBottom = ({datas, currentPage, hide, setHide, languageName}:PropsType) => {
    return(
        <>
            <CustomBookMarkBottom>
                { hide ? <span onClick={()=>{setHide(false)}}>show list </span> : 
                         <span onClick={()=>{setHide(true)}}>hide list </span> }
            </CustomBookMarkBottom>
            <CustomBookMarkBottom className="page">
                    <CustomArrowBox>
                        <Link 
                            style={currentPage !== 0 ? {display:'block'} : {display:'none'}}
                            to={`/postinglist/${languageName}/${datas[currentPage-1]?.frontmatter?.slug}`}> 
                            <SVG width={'500px'}height={'500px'} 
                                pathD={currentPage !== 0 ? SvgImages.leftArrow : ''}></SVG>
                        </Link>
                    </CustomArrowBox>
                    {currentPage+1}/{datas.length} 
                    <CustomArrowBox>
                        <Link
                            style={currentPage < datas.length-1 ? {display:'block'} : {display:'none'}}
                            to={`/postinglist/${languageName}/${datas[currentPage+1]?.frontmatter?.slug}`}>
                            <SVG width={'500px'}height={'500px'} 
                                pathD={currentPage < datas.length-1 ? SvgImages.rightArrow : ''}></SVG>
                        </Link>
                    </CustomArrowBox>
            </CustomBookMarkBottom>
        </>
    )
}

export default BookMarkBottom;