import { Link } from "gatsby";
import React from "react";
import { PostingListType } from "../../../hooks/usePostingList";
import { SytledBlog } from "../../styledComponents/blog/StyledBlog";


type PropsType = {
    datas: PostingListType[];
    currentPage: number;
    languageName: string;
}

const {CustomBookMarkLists, CustomBookMarkList} = SytledBlog.elements;

const BookMarkList = ({datas, currentPage, languageName}:PropsType) => {
    return(
        <CustomBookMarkLists>
            {
                datas.map((data:PostingListType, index:number) => (
                    <CustomBookMarkList key={index}
                    >
                        <Link className={`${index === currentPage ? 'border' : ''}`} to={`/postinglist/${languageName}/${data.frontmatter.slug}`}>
                        {`${index+1}. ${data.frontmatter.title}`}
                        </Link>
                    </CustomBookMarkList>
                ))   
            }
        </CustomBookMarkLists>
    )
}

export default BookMarkList;