import styled, {css} from "styled-components";

const CustomUl = styled.ul`
  width: 80%;
  margin: 1rem auto;
  font-size: 1rem;
`

const CustomLi = styled.li`
  padding: revert;
  list-style: initial;
  margin-left: 2rem;

  ul{
    margin-left: 2rem;
  }
`

const CustomTable = styled.table`

  ${({theme}) => {

    const {colors} = theme;

    return css`
      border: 1px solid ${colors.textColor};
      margin: 1rem auto;
      width: 80%;
      box-shadow: 5px 5px 0px 2px ${colors.textColor};
      text-align: center;
      font-size: 1rem;
      color: ${colors.textColor};

      thead{
        tr{
          th{
            border: 1px solid ${colors.textColor};
            padding: 3%;
          }
        }
      }
    
      tbody{
        tr{
          td{
            border: 1px solid ${colors.textColor};
            padding: 2%;
            vertical-align: middle;
          }
        }
      }
    `
  }}  
`

const CustomBlockquote = styled.blockquote`
      ${({theme}) => {
        
        const {colors} = theme;
        
        return css`

          padding: 2% 0;
          p{
            font-size: larger;
            background: rgba(0,0,0,0.3);
            padding: 3px 0px;
            color: ${colors.textColor};
          }
          p:before {
            content: ' ';
            background-color: ${colors.defaultColor};
            padding-right: 4px;
            margin-right: 10px;
          }
          
        `
      }}
      
    `

const CustomP = styled.p`
    ${({theme}) => {

      const {margins} = theme;

      return css`
        width: 80%;
        margin: ${margins.sm} auto;
      `
    }}
`

const CustomSpan = styled.span`
  max-width: 100%;
`

export const StyledAst = { 
    CustomSpan, 
    CustomUl, 
    CustomLi, 
    CustomTable, 
    CustomBlockquote, 
    CustomP
};
