
import { graphql } from 'gatsby';
import * as React from "react";
import { RecoilRoot, useRecoilState } from "recoil";
import BlogContainer from '../../../components/blog/organisms/BlogContainer';
import { renderAst } from '../../../components/blog/RenderAst';
import Layout from '../../../components/layouts/Layout';
import Main from '../../../components/layouts/Main';
import { blogContentState } from '../../../recoil/BlogContent';
import { parameterNameState } from '../../../recoil/PostingListParamName';


const PostingContent = ({data, params}:any) => {

  const { frontmatter, htmlAst, id} = data.markdownRemark;
  const [blogContent, setBlogContent] = useRecoilState(blogContentState);
  const [paramsName, setParamsName] = useRecoilState(parameterNameState);

  setBlogContent(data.markdownRemark);
  setParamsName(params.name);


  // const init = () => {
  //   setBlogContent(data.markdownRemark);
  //   setParamsName(params.name);
  // }

  // React.useEffect(()=>{
  //   init();

  // },[blogContent]);
  

    return(
      <Layout>
        <Main height={'auto'}>
          <BlogContainer>
            {renderAst(htmlAst) }
          </BlogContainer>
        </Main>
        </Layout>
    )
}


export const query = graphql`
  query ($id: String) {
    markdownRemark(id: {eq: $id}) {
        htmlAst
        frontmatter {
            title
            slug
          }
          id
      }
  }
`

export default PostingContent;